import Vue from 'vue'
import Vuex from 'vuex'

import actions from './actions'

Vue.use(Vuex)
const state = {
    userData:'',
    IndexUrl:'',
    Index:1,
    isAdmin:0
}

if(sessionStorage.getItem('token') != null && sessionStorage.getItem('token') != 'undefined'){
    state.userData = JSON.parse(sessionStorage.getItem('token'))
}
const  getters ={}
const mutations = {
    //用户信息
    modifyuserData(state, param) {
        state.userData = param
        sessionStorage.setItem('token',JSON.stringify(param))
    },
    // 入口链接
    setIndexUrl(state, param){
        state.IndexUrl = param;
     
    },
    //状态
    oneIndex(state, param) {
        state.Index = param;
        console.log(param)
    },
    //点击发布后  存储的是否是管理员
    isAdmin(state,param) {
        state.isAdmin = param;
    }
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
})