import axios from 'axios'
import qs from 'qs'
import {Toast} from 'vant'
import router from '../../router/index'

axios.defaults.timeout = 30000
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded' // 请求头

axios.defaults.baseURL = 'https://testapi.e-power.vip/v1/';
//请求拦截器
axios.interceptors.request.use((config) => {
    console.log(config.method ,config.headers['Content-Type'] === 'multipart/form-data')
    if (config.method  === 'post' && config.headers['Content-Type'] === 'multipart/form-data') {
      return config
    }
    if(config.method === 'post' ) {
        config.data = qs.stringify(config.data)
    }
    return config
}, ()=> {
    return console.log('网络异常')
})
//响应拦截器
axios.interceptors.response.use(function(response) {
    console.log(response)
    if(response.data.err_code == 10003) {
        Toast(response.data.err_msg);
        router.push('/home')
    }
    return response
})

export function fetch(url,params) {
    return new Promise((resolve,reject) => {
        axios.post(url,params).then(response => {
            resolve(response.data)
        }).catch((error) => {
            reject(error)
        })
    })
}
// export default fetch